
.timeline-nav[data-v-2000e4d4] {
  position: fixed;
  left: 0; bottom: 0;
  width: 100%;
  height: 60px;
  padding: 0 20px 40px 20px;
  box-sizing: border-box;
}
.timeline[data-v-2000e4d4] {
  position: fixed;
  right: 20px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.timeline-item[data-v-2000e4d4] {
  width: 50px;
  height: 50px;
  border-radius: 25px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% 100%;
  margin: 8px 0;
  cursor: pointer;
  -webkit-transform: scale(0.5, 0.5);
          transform: scale(0.5, 0.5);
  box-shadow: 0 2px 6px 0 rgba(0,0,0,0.80);
  transition: all 0.2s;
}
.timeline-item[data-v-2000e4d4]:not(.active):hover {
  -webkit-transform: scale(0.8, 0.8);
          transform: scale(0.8, 0.8);
}
.timeline-item.active[data-v-2000e4d4] {
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
  box-shadow: 0 2px 6px 0 #FF6666;
}
.timeline-item-year[data-v-2000e4d4] {
  position: absolute;
  top: 25px;
  left: -14px;
  -webkit-transform: translate(-100%, -50%);
          transform: translate(-100%, -50%);
  white-space: nowrap;
  color: #fff;
  font-family: Menlo-Regular, monospace;
  font-size: 24px;
  opacity: 0.9;
  transition: all 0.2s;
}
.timeline-item.active .timeline-item-year[data-v-2000e4d4], .timeline-item:hover .timeline-item-year[data-v-2000e4d4] {
  left: -7px;
  opacity: 1;
}
.timeline-line[data-v-2000e4d4] {
  position: absolute;
  top: 30px;
  height: calc(100% - 60px);
  left: 20px;
  width: 8px;
  background: #fff;
  border: 1px solid #6157FF;
  z-index: -1;
}
button[data-v-2000e4d4] {
  position: absolute;
  top: 0;
  background: #fff;
  padding: 20px;
  cursor: pointer;
}
.left-button[data-v-2000e4d4] {
  left: 5px;
}
.right-button[data-v-2000e4d4] {
  right: 5px;
}



